import React from 'react'
import {Link} from 'react-router-dom'
import '../CSS/home.css'

class AdminHome extends React.Component{
    render(){
        return(
            <div align="right" className="container" id="main-tag">
            <br/>
                <div className="row">
                    <div className="col-sm-4">
                        <Link to="/adminUserHome"><button className="btn btn-primary">User Management</button></Link>
                    </div>
                    <div className="col-sm-4">
                       <Link to="/adminInventoryHome"><button className="btn btn-primary">Inventory Management</button></Link>
                    </div>
                </div>
            </div>
        )
    }
}
export default AdminHome;
